.navbar {
  display: flex;
  align-items: center;
  width: 100%;
  background-color: white;
  height: 70px;
  top: 0;
  border-bottom: 1px solid #ccc;
  position: fixed;
  z-index: 1;
  
}
  
/* .navbar-logo-image {
  width: 100px;
  height: auto; 
} */

.navbar-logo {
  font-weight: bold;
  font-size: 24px;
  text-decoration: none;
  color: rgb(39, 39, 39);
}

.navbar-logo-link {
  text-decoration: none;
}

.navbar-items {
  display: flex;
}

/* Style the navbar links */
.navbar-items > * {
  margin-left: 16px;
  text-decoration: none;
  font-size: 12px;
  color: rgb(39, 39, 39);
}

.user-info {
  display: flex;
  align-items: center;
  gap: 16px;
}

.user-info a {
  text-decoration: none;
  color: var(--dark-color);
}
